import { Glue42 } from "../../glue";

export interface AppPreferences {
    app: string;
    data: any;
    lastUpdate: Date;
}

export class Prefs {

    constructor(private appName: string, private interop: Glue42.Interop.API) {
    }

    public async get(app?: string): Promise<AppPreferences | undefined> {
        const data = (await this.interop.invoke("T42.Prefs.Get", { app: app ?? this.appName }));
        return data.returned;
    }

    public async set(data: any, options?: { app: string }): Promise<void> {
        await this.interop.invoke("T42.Prefs.Set", { app: options?.app ?? this.appName, data, merge: false });
    }

    public async update(data: any, options?: { app: string }) {
        await this.interop.invoke("T42.Prefs.Set", { app: options?.app ?? this.appName, data, merge: true });
    }

    public async clear(app?: string) {
        await this.interop.invoke("T42.Prefs.Set", { app: app ?? this.appName, clear: true });
    }

    public async getAll(): Promise<AppPreferences[]> {
        const data = (await this.interop.invoke("T42.Prefs.Get"));
        return data.returned;
    }

    public async clearAll() {
        await this.interop.invoke("T42.Prefs.Set", { clear: true });
    }
}
