import { Glue42 } from "../glue";

export class InitInfo {

    public reject: (reason?: any) => void;
    public resolve: (value: Glue42.Glue | PromiseLike<Glue42.Glue>) => void;
    public numberOfCalls: number = 0;

    private promise: Promise<Glue42.Glue>;

    constructor() {
        this.numberOfCalls = 1;
        this.promise = new Promise((resolve, reject) => {
            this.resolve = resolve;
            this.reject = reject;
        });
    }

    public getPromise() {
        this.numberOfCalls++;
        return this.promise;
    }

    public done(g: Glue42.Glue) {
        this.resolve(g);
    }

    public error(e: Error) {
        this.reject(e);
    }
}
