import { Glue42 } from "../../glue";
import { default as CallbackRegistryFactory, CallbackRegistry } from "callback-registry";
import { SharedContextSubscriber } from "./contexts";
import { switchChannel } from "./agm";

export class ChannelsImpl implements Glue42.Channels.API {
    private subsKey = "subs";
    private changedKey = "changed";
    private isInitialJoin = true;

    private registry: CallbackRegistry = CallbackRegistryFactory();
    private currentContext: string;

    constructor(private shared: SharedContextSubscriber, private interop: Glue42.Interop.API, private getWindows: () => Glue42.Windows.API, private logger: Glue42.Logger.API) {
        this.shared.subscribe(this.handler.bind(this));

        if (typeof window !== "undefined") {
            this.currentContext = (window.glue42gd as any).initialChannel;
            if (this.currentContext) {
                this.joinNoSelectorSwitch(this.currentContext);
            }
        }
    }

    public subscribe(callback: (data: object, context: Glue42.Channels.ChannelContext, updaterId: string) => void): () => void {
        if (typeof callback !== "function") {
            throw new Error("Please provide the callback as a function!");
        }
        return this.registry.add(this.subsKey, callback);
    }

    public async subscribeFor(name: string, callback: (data: object, context: Glue42.Channels.ChannelContext, updaterId: string) => void): Promise<() => void> {
        if (typeof name !== "string") {
            throw new Error("Please provide the name as a string!");
        }
        if (typeof callback !== "function") {
            throw new Error("Please provide the callback as a function!");
        }
        const unsubscribeFunc = await this.shared.subscribeFor(name, callback);
        return unsubscribeFunc;
    }

    public async publish(data: object, name?: string): Promise<void> {
        if (typeof data !== "object") {
            throw new Error("Please provide the data as an object!");
        }

        if (name) {
            if (typeof name !== "string") {
                throw new Error("Please provide the name as a string!");
            }

            if (!this.shared.isChannel(name)) {
                return Promise.reject(new Error(`A channel with name: ${name} doesn't exist!`));
            }

            return this.shared.updateData(name, data);
        }

        if (!this.currentContext) {
            throw new Error("Not joined to any channel!");
        }
        return this.shared.updateData(this.currentContext, data);
    }

    public all(): Promise<string[]> {
        const channelNames = this.shared.all();
        return Promise.resolve(channelNames);
    }

    public async list(): Promise<Glue42.Channels.ChannelContext[]> {
        const channelNames = await this.all();
        const channelContexts = await Promise.all(channelNames.map((channelName) => this.get(channelName)));
        return channelContexts;
    }

    public get(name: string): Promise<Glue42.Channels.ChannelContext> {
        if (typeof name !== "string") {
            return Promise.reject(new Error("Please provide the channel name as a string!"));
        }

        return this.shared.getContextData(name);
    }

    /* Returns info about my channel */
    public getMy(): Promise<Glue42.Channels.ChannelContext> {
        if (!this.currentContext) {
            return Promise.resolve(undefined);
        }
        return this.get(this.currentContext);
    }

    public async join(name: string): Promise<void> {
        return this.joinCore(name);
    }

    public async joinNoSelectorSwitch(name: string): Promise<void> {
        return this.joinCore(name, false);
    }

    public leave() {
        return this.leaveCore();
    }

    public leaveNoSelectorSwitch() {
        return this.leaveCore(false);
    }

    public current(): string {
        return this.currentContext;
    }

    public my(): string {
        return this.current();
    }

    public changed(callback: (channel: string) => void): () => void {
        if (typeof callback !== "function") {
            throw new Error("Please provide the callback as a function!");
        }

        return this.registry.add(this.changedKey, callback);
    }

    public onChanged(callback: (channel: string) => void): () => void {
        return this.changed(callback);
    }

    public async add(info: Glue42.Channels.ChannelContext): Promise<Glue42.Channels.ChannelContext> {
        if (typeof info !== "object") {
            throw new Error("Please provide the info as an object!");
        }
        if (typeof info.name === "undefined") {
            throw new Error("info.name is missing!");
        }
        if (typeof info.name !== "string") {
            throw new Error("Please provide the info.name as a string!");
        }
        if (typeof info.meta === "undefined") {
            throw new Error("info.meta is missing!");
        }
        if (typeof info.meta !== "object") {
            throw new Error("Please provide the info.meta as an object!");
        }
        if (typeof info.meta.color === "undefined") {
            throw new Error("info.meta.color is missing!");
        }
        if (typeof info.meta.color !== "string") {
            throw new Error("Please provide the info.meta.color as a string!");
        }

        const context: Glue42.Channels.ChannelContext = {
            name: info.name,
            meta: info.meta || {},
            data: info.data || {}
        };

        // Note that we use `update` instead of `add` so that if the channel already exists we don't overwrite it.
        await this.shared.updateChannel(info.name, context);

        return context;
    }

    public async getWindowsOnChannel(channel: string): Promise<Glue42.Windows.GDWindow[]> {
        const windowInfos = await this.getWindowsWithChannels({ channels: [channel] });
        return windowInfos.map((w) => w.window);
    }

    public async getWindowsWithChannels(filter: Glue42.Channels.WindowWithChannelFilter): Promise<Glue42.Channels.WindowOnChannelInfo[]> {
        try {
            const info = await this.interop.invoke<{ windows: Array<{ windowId: string, channel: string, application: string }> }>("T42.Channels.Announce", { command: "getChannelsInfo", data: { filter } });
            const windows = this.getWindows();
            if (info?.returned?.windows) {
                return info.returned.windows.map((windowInfo) => {
                    const window = windows.findById(windowInfo.windowId);
                    return {
                        window,
                        channel: windowInfo.channel,
                        application: windowInfo.application
                    };
                });
            }
        } catch (er) {
            this.logger.error(`Error getting all channel enabled windows. This method is available since Glue42 3.12`, er);
        }
        return [];
    }

    private handler(data: object, context: Glue42.Channels.ChannelContext, updaterId: string): any {
        this.registry.execute(this.subsKey, data, context, updaterId);
    }

    private async joinCore(name: string, changeSelector: boolean = true) {
        if (typeof name !== "string") {
            throw new Error("Please provide the channel name as a string!");
        }

        if (!this.isInitialJoin && name === this.currentContext) {
            // ignore joining the channel that we're already on, except when this is the very first call
            return;
        }
        this.isInitialJoin = false;

        // Checks if a channel exists with the provided name.
        const doesChannelExist = (channelName: string): boolean => {
            const channelNames = this.shared.all();
            return channelNames.includes(channelName);
        };

        // Wait for 3k ms for the channel to appear if it doesn't already exist.
        if (!doesChannelExist(name)) {
            const channelExistsPromise = new Promise<void>((resolve, reject) => {
                let timeoutId: NodeJS.Timeout;

                // Check every 100 ms for a channel with the provided name.
                const intervalId = setInterval(() => {
                    if (doesChannelExist(name)) {
                        clearTimeout(timeoutId);
                        clearInterval(intervalId);
                        resolve();
                    }
                }, 100);

                timeoutId = setTimeout(() => {
                    clearInterval(intervalId);

                    return reject(new Error(`A channel with name: ${name} doesn't exist!`));
                }, 3000);
            });

            await channelExistsPromise;
        }

        this.currentContext = name;
        await this.shared.switchChannel(name);
        if (changeSelector) {
            switchChannel(name);
        }
        this.registry.execute(this.changedKey, name);
    }

    private leaveCore(changeSelector = true): Promise<void> {
        this.currentContext = undefined;
        this.registry.execute(this.changedKey, undefined);
        this.shared.unsubscribe();
        if (changeSelector) {
            switchChannel();
        }
        return Promise.resolve();
    }
}
