import * as AgmNames from "./const/agm-names";
import { objectValues } from "./helper";
import { Glue42Core } from "@glue42/core";
import AppManagerImpl from "./app-manager";

/** Retrieves application snapshot from ACS */
export default function snapshot(agm: Glue42Core.AGM.API, appManager: AppManagerImpl): Promise<undefined> {
    return new Promise((resolve, reject) => {
        agm.invoke(AgmNames.GetApplicationsMethodName, { skipIcon: true })
            .then((response) => {
                const data = response.returned;
                if (!data) {
                    resolve();
                }

                const applications = data.applications;
                if (!applications) {
                    resolve();
                }

                objectValues(applications).map((item) => appManager.handleAppAdded(item));

                resolve();
            })
            .catch((err) => reject(`Error getting application snapshot: ${err.message}`));
    });
}
