    public async moveResize(w: Glue42.Windows.GDWindow, bounds: Partial<Glue42.Windows.Bounds>): Promise<Glue42.Windows.GDWindow> {
        if ((window.glueDesktop as any).versionNum < 31200) {
            return new Promise(async (res, rej) => {
                const resolveImmediately = this.areBoundsEqual(bounds, w);
                let isDone = false;
                const done = () => {
                    if (isDone) {
                        return;
                    }
                    isDone = true;

                    if (unsubscribeBoundsChanged) {
                        unsubscribeBoundsChanged();
                        unsubscribeBoundsChanged = undefined;
                    }
                    res(w);
                    if (resolveTimeout) {
                        clearTimeout(resolveTimeout);
                        resolveTimeout = undefined;
                    }
                };
                let resolveTimeout: any;
                let unsubscribeBoundsChanged: () => void;
                if (!resolveImmediately) {
                    unsubscribeBoundsChanged = w.onBoundsChanged((win: Glue42.Windows.GDWindow) => {
                        if (!this.areBoundsEqual(bounds, win)) {
                            return;
                        }
                        done();
                    });
                }

                try {
                    // always execute moveResize (we might have old bounds and prevent calls that are actually valid)
                    await this.execute("moveResize", { windowId: w.id, options: { bounds } });
                } catch (error) {
                    rej(error);
                    return;
                }

                // if we're trying to resize with the bounds we already have
                if (resolveImmediately) {
                    done();
                    return;
                }

                // if no bounds change in the next 1s resolve
                resolveTimeout = setTimeout(() => {
                    done();
                }, 1000);
            });
        } else {
            await this.execute("moveResize", { windowId: w.id, options: { bounds } });
        }
        return w;
    }

