    public async updateContext(w: Glue42.Windows.GDWindow, context: any): Promise<Glue42.Windows.GDWindow> {
        let un: UnsubscribeFunction;
        try {
            // as undefined values do not go through interop, setting a prop to undefined will be lead to no change
            // set this to null so it actually removes the prop
            const contextWithoutUndefinedValues = this.swapUndefinedToNull(context);

            const done = new Promise<void>((resolve, reject) => {
                un = w.onContextUpdated(() => {
                    resolve();
                });
            });
            await Promise.all([this.execute("updateContext", {
                windowId: w.id, context: contextWithoutUndefinedValues, replace: Object.keys(w.context).length > 0 ? false : true
            }), done]);
            return w;
        } catch (error) {
            throw error;
        } finally {
            if (un) {
                un();
            }
        }
    }

