    public async showPopup(targetWindow: Glue42.Windows.GDWindow, options: Glue42.Windows.PopupOptions): Promise<Glue42.Windows.GDWindow> {
        if (!options) {
            throw new Error("The options object is not valid!");
        }

        const optionsCopy = { ...options };

        if (!optionsCopy.targetLocation) {
            optionsCopy.targetLocation = "bottom" as Glue42.Windows.PopupTargetLocation;
        }

        const reformatedOptions: ReformatedPopupOptions & { targetId: string } = {
            ...optionsCopy,
            popupBounds: optionsCopy.size,
            targetId: targetWindow.id,
            popupId: optionsCopy.windowId
        };

        await this.execute("showPopupWindow", {
            windowId: targetWindow.id,
            options: reformatedOptions
        });

        return targetWindow;
    }

    public async createFlydown(windowId: string, options: Glue42.Windows.FlydownOptions) {
        if (!options) {
            throw new Error("The options object is not valid!");
        }

        const optionsCopy = { ...options };

        if (!optionsCopy.horizontalOffset) {
            optionsCopy.horizontalOffset = 0;
        }

        if (!optionsCopy.verticalOffset) {
            optionsCopy.verticalOffset = 0;
        }

        const fullOptions: Glue42.Windows.FlydownOptions & { targetId: string, flydownBounds: Glue42.Windows.FlydownOptions["size"] } = this.reformatFlydownOptions(windowId, optionsCopy);

        return this.execute("setFlydownArea", { windowId, options: fullOptions }).then(() => {
            const zoneIds = fullOptions.zones.map((z) => z.id);

            fullOptions.zones.forEach((z) => {
                let callback: (...args: any[]) => any = typeof (z.flydownSize) === "function" ?
                    z.flydownSize : () => z.flydownSize;

                if (options.size instanceof Function && z.flydownSize) {
                    callback = async (data: Glue42.Windows.ShowFlydownData, cancel: () => void) => {
                        let result;
                        // The options callback must be always invoked
                        // If the property in the zone is null it will be automatically
                        // assigned to the same callback from the code above reformatFlydownOptions
                        if (options.size instanceof Function) {
                            result = await options.size(data, cancel);
                        }

                        // If both are callbacks the zone has higher priority
                        // If they are the same only one should be invoked
                        if (z.flydownSize instanceof Function && z.flydownSize !== options.size) {
                            return await z.flydownSize(data, cancel) || result;
                        }

                        // The options callback overwrites the bounds object specified in the zone
                        return result || z.flydownSize;
                    };
                }
                this._registry.clearKey(`${fullOptions.targetId}_${z.id}`);
                this._registry.add(`${fullOptions.targetId}_${z.id}`, callback);
            });

            return {
                destroy: () => this.clearFlydownArea(fullOptions.targetId, zoneIds),
                options: optionsCopy
            };
        });
    }

