    public async place(window: Glue42.Windows.GDWindow, options: Glue42.Windows.PlacementSettings): Promise<void> {
        const copy = { ...options } as any;
        if (!options.display || options.display === "current") {
            copy.display = await window.getDisplay();
        }
        // Glue42.Displays.API works with zero based array of displays, which means that the first monitor has index 0
        // whereas GD works with non zero based array of displays, which means that the first monitor has index 1
        copy.display = copy.display.index + 1;
        return this.execute(
            "place",
            { windowId: window.id, options: { ...copy } }
        );
    }

