import * as AgmNames from "./const/agm-names";
import * as EventNames from "./const/event-names";
import { objectValues } from "./helper";
import { Glue42Core } from "@glue42/core";
import { Glue42 } from "../../glue";
import { AppProps, InstanceProps } from "./types";
import AppManagerImpl from "./app-manager";
import EntitlementsImpl from "./entitlements";

/** Subscribe for the AGM stream published by ACS and distributes events to applications and instances components */
export default function createDataSubscription(agm: Glue42Core.AGM.API, applications: AppManagerImpl, entitlements: EntitlementsImpl, skipIcons: boolean) {

    let subscription: Glue42Core.AGM.Subscription;
    const start = () => {
        let resolveFunc: (value?: {} | Promise<{}>) => void;
        let rejectFunc: (error?: any) => void;

        const resultPromise = new Promise((resolve, reject) => {
            resolveFunc = resolve;
            rejectFunc = reject;
        });

        agm.subscribe(AgmNames.OnEventMethodName, { arguments: { skipIcon: skipIcons }, waitTimeoutMs: 10000 })
            .then((s) => {
                subscription = s;
                subscription.onData((streamData: Glue42Core.AGM.StreamData) => {
                    const events: any = streamData.data;

                    objectValues(events[EventNames.OnApplicationAddedEvent])
                        .map((item: AppProps) => applications.handleAppAdded(item));

                    objectValues(events[EventNames.OnApplicationChangedEvent])
                        .map((item: AppProps) => applications.handleAppUpdated(item));

                    objectValues(events[EventNames.OnApplicationRemovedEvent])
                        .map((item: AppProps) => applications.handleAppRemoved(item));

                    objectValues(events[EventNames.OnApplicationReadyEvent])
                        .map((item: AppProps) => applications.handleAppReady(item));

                    objectValues(events[EventNames.OnApplicationStartedEvent])
                        .map((item: InstanceProps) => applications.handleInstanceStarted(item));

                    objectValues(events[EventNames.OnApplicationStartFailedEvent])
                        .map((item: InstanceProps) => applications.handleInstanceStartFailed(item));

                    objectValues(events[EventNames.OnApplicationStoppedEvent])
                        .map((item: InstanceProps) => applications.handleInstanceStopped(item));

                    objectValues(events[EventNames.OnApplicationUpdatedEvent])
                        .map((item: InstanceProps) => applications.handleInstanceUpdated(item));

                    objectValues(events[EventNames.OnApplicationAgmServerReadyEvent])
                        .map((item: InstanceProps) => applications.handleInstanceAgmServerReady(item));

                    objectValues(events[EventNames.OnBranchChangedEvent])
                        .map((item: Glue42.AppManager.Branch) => entitlements.handleBranchModified(item));

                    objectValues(events[EventNames.OnBranchesModifiedEvent])
                        .map((item: Glue42.AppManager.Branch[]) => entitlements.handleBranchesModified(item));

                    resolveFunc();
                });

                subscription.onFailed((err: any) => rejectFunc(err));

            })
            .catch((err: any) => rejectFunc(`Error subscribing for ${AgmNames.OnEventMethodName} stream. Err: ${err}`));

        return resultPromise;
    };

    const stop = () => {
        if (subscription) {
            subscription.close();
        }
    };

    return {
        start,
        stop
    };
}
