import { Glue42 } from "../../glue";
import { CallbackRegistry, UnsubscribeFunction } from "callback-registry";
import { LayoutsStream, HibernationResumeMessage, UpdateLayoutComponentContextMessage } from "./types";
import store from "./store";
import generate from "shortid";
import { ACSSaveLayoutMessage, ACSRestoreMessage, ACSRestoreMessageContext, ACSRestoreMessageInstanceInfo, ACSMessage, ACSRenameMessage, ACSRemoveLayoutMessage, ACSImportMessage, ExportResultMessage } from "./types";
import { Glue42Core } from "@glue42/core";
import ContextProvider from "./contextProvider";
import transformACSLayout from "./transform";
import LayoutImpl from "./layout";

export default class LayoutsAPIImpl implements Glue42.Layouts.API {
    private appManager: Glue42.AppManager.API;
    private provider: ContextProvider;

    constructor(private config: Glue42.Layouts.Configuration, private stream: LayoutsStream, private callbacks: CallbackRegistry, logger: Glue42.Logger.API) {
        this.appManager = config.appManager;
        this.onEvent = stream.onEvent;
        this.provider = new ContextProvider(config, config.activityGetter, callbacks, logger);
        stream.subscribe();
    }

    public async setDefaultGlobal(name: string): Promise<void> {
        await this.config.agm.invoke("T42.ACS.SelectDefaultLayout", { name });
        return;
    }

    public async clearDefaultGlobal(): Promise<void> {
        await this.config.agm.invoke("T42.ACS.DeselectDefaultLayout");
        return;
    }

    public async getDefaultGlobal(): Promise<Glue42.Layouts.Layout | undefined> {
        const result = await this.config.agm.invoke("T42.ACS.GetDefaultLayout");
        const layout = result.returned;
        if (!layout) {
            return undefined;
        }
        if (this.isSlimMode()) {
            return layout;
        }
        return this.list().find((l) => l.name === layout.name && l.type === "Global");
    }

    public ready(): Promise<void> {
        if (this.config.mode === "fullWaitSnapshot") {
            return this.stream.gotSnapshot;
        }
        return this.stream.ready;
    }

    public save(layout: Glue42.Layouts.NewLayoutOptions): Promise<Glue42.Layouts.Layout> {
        return new Promise<Glue42.Layouts.Layout>((resolve, reject) => {

            this.verifyNotSlimMode();

            if (!layout.name) {
                throw Error("layout.name argument is required");
            }

            if (!layout.type) {
                layout.type = "Global";
            }

            if (layout.activityId) {
                layout.type = "Activity";
            }

            if (typeof layout.ignoreHidden === "undefined") {
                layout.ignoreHidden = true; // default to true
            }

            const layoutObject: ACSSaveLayoutMessage = {
                name: layout.name,
                actIds: [],
                appIds: [],
                type: layout.type,
                context: layout.context,
                metadata: layout.metadata || {},
                options: { ignoreLayoutRestrictions: false },
            };

            if (layout.type === "Activity") {
                let actId = layout.activityId;
                if (!actId) {
                    if (!this.appManager.myInstance.inActivity) {
                        throw new Error("Current application is not in activity. Can not save activity layout for it");
                    }
                    actId = this.appManager.myInstance.activityId;
                }

                layoutObject.actIds.push(actId);
                layoutObject.options = { ignoreLayoutRestrictions: true };
            } else if (layout.type === "Global") {
                if (this.config.gdMajorVersion === 2) {
                    let instances: any[] = this.appManager.instances();

                    if (layout.ignoreHidden) {
                        instances = instances.filter((i) => this.isVisibleWindow(i));
                    }

                    if (layout.ignoreMyInstance && this.appManager.myInstance) {
                        instances = instances.filter((i) => i.id !== this.appManager.myInstance.id);
                    }

                    instances.reduce((prev, current) => {
                        if (!current.id) {
                            return prev;
                        }

                        if (current.inActivity) {
                            const actId = current.activityId;

                            if (prev.actIds.indexOf(actId) === -1) {
                                prev.actIds.push(actId);
                            }
                        } else {
                            prev.appIds.push(current.id);
                        }

                        return prev;
                    }, layoutObject);
                } else {
                    layoutObject.autoCollectApps = true;
                }
            } else {
                throw new Error("layout type " + layout.type + " not supported");
            }

            this.invokeMethodAndTrack("T42.ACS.SaveLayout", layoutObject, resolve, reject);
        });
    }

    public restore(options: Glue42.Layouts.RestoreOptions): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            this.verifyNotSlimMode();

            if (typeof options === "undefined") {
                throw Error("options argument is required");
            }

            if (!options.name) {
                throw Error("options.name argument is required");
            }

            if (!options.type) {
                options.type = "Global";
            }

            if (options.activityIdToJoin) {
                options.type = "Activity";
            }

            if (typeof options.restoreActivityOwner === "undefined") {
                options.restoreActivityOwner = false;
            }

            if (typeof options.ignoreActivityWindowTypes === "undefined") {
                options.ignoreActivityWindowTypes = [];
            }

            if (typeof options.setActivityContext === "undefined") {
                options.setActivityContext = true;
            }

            // if no closeOwnRunning we set closeRunningInstance to true; if
            if (typeof options.closeRunningInstance === "undefined") {
                if (options.type === "Global") {
                    options.closeRunningInstance = true;
                } else if (options.type === "Activity") {
                    // we don't close anything in case of activity layout type
                    options.closeRunningInstance = false;
                }
            }

            if (typeof options.reuseWindows === "undefined") {
                options.reuseWindows = true;
            }

            options.context = options.context || {};

            const t42: ACSRestoreMessageContext = {
                restoreOptions: {
                    activityToJoin: options.activityIdToJoin,
                    setActivityContext: options.setActivityContext,
                    ignoreActivityWindowTypes: options.ignoreActivityWindowTypes,
                    restoreActivityOwner: options.restoreActivityOwner,
                    closeOldWindows: options.closeRunningInstance,
                    reuseExistingWindows: options.reuseWindows
                }
            };

            const arg: ACSRestoreMessage = {
                type: options.type,
                name: options.name,
                context: options.context,
                toClose: [],
                splash: options.splash
            };

            // getting instance for closing is necessary only for GD2
            // G4E-2418 - A global layout including swimlane app can not be restored, when the activities configuration is set to false
            if (this.config.gdMajorVersion === 2) {
                if (options.closeRunningInstance) {
                    arg.toClose = this.getInstancesToClose(options);
                }
            }

            if (options.timeout) {
                arg.timeout = options.timeout;
            }

            arg.context._t42 = t42;

            this.invokeMethodAndTrack("T42.ACS.RestoreLayout", arg, resolve, reject, true);
        });
    }

    public remove(type: string, name: string): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            this.verifyNotSlimMode();

            if (!name) {
                throw Error("name argument is required");
            }

            if (!type) {
                throw Error("type argument is required");
            }

            const msg: ACSRemoveLayoutMessage = {
                type,
                name,
            };

            this.invokeMethodAndTrack("T42.ACS.RemoveLayout", msg, resolve, reject);
        });
    }

    public list(): Glue42.Layouts.Layout[] {
        this.verifyNotSlimMode();
        return store.all;
    }

    public import(layouts: Glue42.Layouts.Layout[], mode?: Glue42.Layouts.ImportMode): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            this.verifyNotSlimMode();

            const msg: ACSImportMessage = {
                mode: mode || "replace",
                layouts
            };

            this.invokeMethodAndTrack("T42.ACS.ImportLayouts", msg, resolve, reject, true);
        });
    }

    public export(layoutType?: Glue42.Layouts.LayoutType): Promise<Glue42.Layouts.Layout[]> {
        return new Promise<Glue42.Layouts.Layout[]>((resolve, reject) => {
            const handleResult = (result: ExportResultMessage) => {
                let layouts: Glue42.Layouts.Layout[] = this.getObjectValues(result.Layouts).map((t) => new LayoutImpl(transformACSLayout(t)));
                if (layoutType) {
                    layouts = layouts.filter((l) => l.type === layoutType);
                }
                resolve(layouts);
            };

            this.invokeMethodAndTrack("T42.ACS.ExportLayouts", {}, handleResult, reject, true);
        });
    }

    public rename(layout: Glue42.Layouts.Layout, newName: string): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            this.verifyNotSlimMode();

            if (!layout) {
                throw Error("layout argument is required");
            }

            if (!layout.name) {
                throw Error("name argument is required");
            }

            if (!layout.type) {
                throw Error("type argument is required");
            }

            const msg: ACSRenameMessage = { type: layout.type, oldName: layout.name, newName };

            this.invokeMethodAndTrack("T42.ACS.RenameLayout", msg, resolve, reject);
        });
    }

    public updateMetadata(layout: Glue42.Layouts.Layout): Promise<void> {
        return new Promise((resolve, reject) => {
            if (!layout) {
                throw Error("layout argument is required");
            }

            if (!layout.name) {
                throw Error("name argument is required");
            }

            if (!layout.type) {
                throw Error("type argument is required");
            }

            if (!layout.metadata) {
                throw Error("metadata argument is required");
            }

            const layoutObject: any = {
                name: layout.name,
                type: layout.type,
                metadata: layout.metadata
            };

            this.invokeMethodAndTrack("T42.ACS.UpdateMetadata", layoutObject, resolve, reject, true);
        });
    }

    public hibernate(name: string, options?: Glue42.Layouts.HibernationOptions): Promise<void> {
        return new Promise((resolve, reject) => {
            if (!name) {
                return reject("name cannot be empty");
            }
            options = options || {};
            const request: HibernationResumeMessage = {
                name,
                type: "Global",
                context: options.context || {},
                metadata: options.metadata || {},
            };
            this.invokeMethodAndTrack("T42.ACS.HibernateLayout", request, resolve, reject, true);
        });
    }

    public resume(name: string, context?: any, options?: Glue42.Layouts.ResumeOptions): Promise<void> {
        return new Promise((resolve, reject) => {
            if (!name) {
                return reject("name cannot be empty");
            }
            const request: HibernationResumeMessage = {
                name,
                type: "Global",
                context,
                ...options
            };
            this.invokeMethodAndTrack("T42.ACS.ResumeLayout", request, resolve, reject, true);
        });
    }

    public async getCurrentLayout(): Promise<Glue42.Layouts.Layout | undefined> {
        const result = await this.config.agm.invoke("T42.ACS.GetCurrentLayout", {}, "best", { methodResponseTimeoutMs: 120 * 1000 });
        let layout = result.returned.layout;
        if (!layout) {
            return undefined;
        }
        if (!this.isSlimMode()) {
            // we have internal if we track layouts (so not slim mode), if not return the object we received from GD
            layout = this.list().find((l) => l.name === layout.name && l.type === layout.type);
        }
        return layout;
    }

    public onAdded(callback: (layout: Glue42.Layouts.Layout) => void): () => void {
        const result = this.callbacks.add("added", callback);

        if (store.all.length > 0) {
            store.all.forEach((layout) => {
                try {
                    callback(layout);
                } catch (err) { /** */ }
            });
        }

        return result;
    }

    public onRemoved(callback: (layout: Glue42.Layouts.Layout) => void): () => void {
        return this.callbacks.add("removed", callback);
    }

    public onChanged(callback: (layout: Glue42.Layouts.Layout) => void): () => void {
        return this.callbacks.add("changed", callback);
    }

    public onRestored(callback: (layout: Glue42.Layouts.Layout) => void): () => void {
        return this.callbacks.add("restored", callback);
    }

    public onRenamed(callback: (layout: Glue42.Layouts.Layout) => void): () => void {
        return this.callbacks.add("renamed", callback);
    }

    public onEvent(callback: (event: any) => void): () => void {
        return this.stream.onEvent(callback);
    }

    public onSaveRequested(callback: (info?: Glue42.Layouts.SaveRequestContext) => Glue42.Layouts.SaveRequestResponse): UnsubscribeFunction {
        return this.provider.onSaveRequested(callback);
    }

    public updateAppContextInCurrent(context: object): Promise<void> {
        return new Promise((resolve, reject) => {
            if (context && typeof context !== "object") {
                reject("context must be an object");
            }
            context = context ?? {};
            const request: UpdateLayoutComponentContextMessage = {
                context
            };
            this.invokeMethodAndTrack("T42.ACS.UpdateLayoutComponentContext", request, resolve, reject, true);
        });
    }

    public updateDefaultContext(context: object): Promise<void> {
        return new Promise((resolve, reject) => {
            if (context && typeof context !== "object") {
                reject("context must be an object");
            }
            context = context ?? {};
            const request: UpdateLayoutComponentContextMessage = {
                context
            };
            this.invokeMethodAndTrack("T42.ACS.UpdateDefaultContext", request, resolve, reject, true);
        });
    }

    public get(name: string, type: Glue42.Layouts.LayoutType): Promise<Glue42.Layouts.Layout> {
        const matching = this.list().find((l) => l.name === name && l.type === type);
        if (!matching) {
            return Promise.reject(`can not find layout with name=${name} and type=${type}`);
        }
        return Promise.resolve(matching);
    }

    public getAll(type: Glue42.Layouts.LayoutType): Promise<Glue42.Layouts.LayoutSummary[]> {
        const matching = this.list().filter((l) => type === l.type);
        return Promise.resolve(matching);
    }

    private isSlimMode() {
        return this.config.mode === "slim";
    }

    private verifyNotSlimMode() {
        if (this.isSlimMode()) {
            throw Error("Operation not allowed in slim mode. Run in full mode.");
        }
    }

    private isVisibleWindow(instance: Glue42.AppManager.Instance) {
        // GD3
        // allow exes to participate in layouts (TODO - check if the app is layouts aware (has the layouts methods) - if not treat it as not visible window)
        if (instance.application.type === "exe" || instance.application.type === "node") {
            return true;
        }
        // allow activities to participate in layouts
        if (instance.application.type === "activity") {
            return true;
        }

        if (!instance || !instance.window) {
            return false;
        }

        return instance.window.isVisible;
    }

    private invokeMethodAndTrack(methodName: string, args: ACSMessage, resolve: (result: any) => void, reject: (err: any) => void, skipStreamEvent?: boolean) {
        let streamEventReceived = skipStreamEvent;
        let agmResult: any;

        const token = generate();
        args.token = token;

        const handleResult = () => {
            if (streamEventReceived && agmResult) {
                resolve(agmResult);
            }
        };

        if (!skipStreamEvent) {
            this.stream.waitFor(token)
                .then(() => {
                    streamEventReceived = true;
                    handleResult();
                })
                .catch((err: any) => {
                    reject(err);
                });
        }

        const responseHandler = (result: Glue42Core.AGM.InvocationResult<any>) => {
            if (!result.returned) {
                reject("No result from method " + methodName);
            }

            if (result.returned.status && (result.returned.status !== "Success" && result.returned.status !== "PartialSuccess")) {
                reject(result.returned);
            }

            agmResult = result.returned;
            handleResult();
        };

        this.config.agm.invoke(methodName, args, "best", { methodResponseTimeoutMs: 120 * 1000 })
            .then(responseHandler)
            .catch((err: any) => reject(err));
    }

    private getInstancesToClose(options: Glue42.Layouts.RestoreOptions) {
        const instances: ACSRestoreMessageInstanceInfo[] = [];
        const shellApp = this.appManager.applications().filter((a: any) => a.isShell)[0];
        const shellApplicationName = shellApp ? shellApp.name : "AppManager";

        this.appManager.instances().forEach((i: any) => {

            // ignore my instance
            if (this.appManager.myInstance && i.id === this.appManager.myInstance.id) {
                return;
            }

            // ignore shell app
            if (i.application.name === shellApplicationName) {
                return;
            }

            // ignore hidden windows
            if (!this.isVisibleWindow(i)) {
                return;
            }

            // if activity layout stop only the instances
            // running in the same activity
            if (options.type === "Activity") {
                if (i.activityId !== options.activityIdToJoin) {
                    return;
                }
            }
            instances.push({ application: i.application.name, instance: i.id });
        });

        return instances;
    }

    private getObjectValues(obj: any) {
        if (!obj) {
            return [];
        }

        return Object.keys(obj).map((k) => obj[k]);
    }
}
