import * as AgmNames from "./const/agm-names";
import { default as CallbackRegistryFactory, CallbackRegistry } from "callback-registry";
import { Glue42 } from "../../glue";
import { ContextParam, InstanceProps, AppProps } from "./types";
import AppManagerImpl from "./app-manager";
import { Glue42Core } from "@glue42/core";
import { objectClone } from "./helper";

export default class ApplicationImpl implements Glue42.AppManager.Application {

    private _registry: CallbackRegistry = CallbackRegistryFactory();

    // Additional properties of the Application
    private _props: AppProps;

    constructor(private _appManager: AppManagerImpl, private _name: string, private _agm: Glue42Core.AGM.API) {

        // TODO: Replace string events with constants. The string declaration of the registry event name should only ever appear once. Ever. Period.

        // subscribe for events from app-manager
        _appManager.onInstanceStarted((instance) => {
            if (instance.application && instance.application.name !== this._name) {
                return;
            }

            this._registry.execute("instanceStarted", instance);
        });

        _appManager.onInstanceStopped((instance) => {
            if (instance.application && instance.application.name !== this._name) {
                return;
            }

            this._registry.execute("instanceStopped", instance);
        });

        _appManager.onAppRemoved((app) => {
            if (app.name !== this._name) {
                return;
            }

            this._registry.execute("appRemoved", app);
        });

        _appManager.onAppChanged((app) => {
            if (app.name !== this._name) {
                return;
            }

            this._registry.execute("appChanged", app);
        });

        _appManager.onAppAvailable((app) => {
            if (app.name !== this._name) {
                return;
            }

            this._registry.execute("appAvailable", app);
        });

        _appManager.onAppUnavailable((app) => {
            if (app.name !== this._name) {
                return;
            }

            this._registry.execute("appUnavailable", app);
        });
    }

    get name(): string { return this._name; }
    get title(): string { return this._props.Title; }
    get version(): string { return this._props.Version; }
    get autoStart(): boolean { return this._props.AutoStart; }
    get isShell(): boolean { return this._props.IsShell; }
    get caption(): string { return this._props.Caption; }
    get hidden(): boolean { return this._props.IsHidden; }
    get container(): string { return this._props.ApplicationName; }
    get activityType(): string { return this._props.ActivityType; }
    get activityWindowType(): string { return this._props.ActivityWindowType; }
    get windowSettings(): { [key: string]: any } {
        if (!this._props.Arguments) {
            return {};
        }
        return objectClone(this._props.Arguments);
    }
    get allowMultiple(): boolean { return this._props.AllowMultiple; }
    get available(): boolean { return this._props.IsReady || true; }
    get icon(): string { return this._props.Icon; }
    get iconURL(): string { return this._props.IconUrl; }
    get sortOrder(): number { return this._props.SortOrder; }
    get userProperties(): { [key: string]: any } {
        if (!this._props.UserProperties) {
            return {};
        }
        return objectClone(this._props.UserProperties);
    }
    get isActivity(): boolean {
        return this._props.ActivityType !== undefined && this._props.ActivityType !== "";
    }

    get configuration(): Glue42.AppManager.LegacyAppConfigProperties {
        return {
            autoStart: this._props.AutoStart,
            caption: this._props.Caption,
            hidden: this._props.IsHidden,
            container: this._props.ApplicationName,
            activityType: this._props.ActivityType,
            allowMultiple: this._props.AllowMultiple
        };
    }

    get instances() {
        return this._appManager.instances().filter((instance: Glue42.AppManager.Instance) => instance.application.name === this._name);
    }

    get type() {
        return this._props.Type;
    }

    get mode() {
        if (!this._props) {
            // no info about the app
            return "unknown";
        }

        // GD3 - we always have Mode from the stream object
        if (this._props.Mode && typeof this._props.Mode === "string") {
            return this._props.Mode.toLowerCase() as Glue42.Windows.WindowMode;
        }

        // GD2
        // return unknown for activity applications
        if (this.isActivity) {
            return "unknown";
        }

        // check definition -> hc.args.mode
        if (this._props.Arguments && this._props.Arguments.mode && typeof this._props.Arguments.mode === "string") {
            return this._props.Arguments.mode.toLowerCase() as Glue42.Windows.WindowMode;
        }

        // if no hc.args.mode, check hc.WindowStyleAttributes
        let styleAttributes = this._props.WindowStyleAttributes;
        if (styleAttributes) {
            // WindowStyleAttributes is not a valid JSON, we will need to find mode in the string
            styleAttributes = styleAttributes.split(" ").join("");
            const searchFor = "mode:\"";
            const modeIndex = styleAttributes.indexOf(searchFor);
            if (modeIndex !== -1) {
                const startModeIndex = modeIndex + searchFor.length;
                const stopModeIndex = styleAttributes.indexOf("\"", startModeIndex);
                const style = styleAttributes.substr(startModeIndex, stopModeIndex - startModeIndex);
                if (style && typeof style === "string") {
                    return style.toLowerCase() as Glue42.Windows.WindowMode;
                }
            }
        }

        // default mode for GD2
        return "flat";
    }

    public updateFromProps(props: AppProps) {
        if (!this._props) {
            this._props = { Name: props.Name };
        }

        Object.keys(props).forEach((key) => {
            (this._props as any)[key] = (props as any)[key];
        });
    }

    public start(context?: ContextParam, options?: Glue42.AppManager.ApplicationStartOptions): Promise<Glue42.AppManager.Instance> {
        const name = this._name;
        const startTimeout: number = 10000;
        return new Promise<Glue42.AppManager.Instance>((resolve, reject) => {
            options = options || {};
            context = context || {};
            const waitForAGMInstance = options.waitForAGMReady ?? true;

            const waitForApplicationInstance = (id: string) => {
                // fetch from the current list in app-manager
                const check = (): Glue42.AppManager.Instance => {
                    const filtered = this._appManager.instances().filter((i: Glue42.AppManager.Instance) => i.id === id);
                    let result: Glue42.AppManager.Instance;
                    // GD2 check - in case of activities we have 2 instances with the same id
                    if (filtered.length === 2) {
                        result = filtered[0].isActivityInstance ? filtered[0] : filtered[1];
                    } else {
                        result = filtered[0];
                    }
                    if (result && waitForAGMInstance) {
                        if (result.agm) {
                            return result;
                        } else {
                            // if no agm, return undefined so we keep waiting
                            return undefined;
                        }
                    }

                    return result;
                };

                let unsub: () => void;
                // reject after some timeout
                const timeout = setTimeout(() => {
                    if (unsub) {
                        unsub();
                    }
                    reject("timeout");
                }, startTimeout);

                const waitFunc = (i: Glue42.AppManager.Instance) => {
                    if (i.id !== id) {
                        return;
                    }
                    if (unsub) {
                        unsub();
                        unsub = undefined;
                    }
                    clearTimeout(timeout);
                    // schedule - we need this because there might be more than one
                    // instance with the same id (activity case) - so this handler
                    // will kick for the very first
                    setTimeout(() => {
                        resolve(check());
                    }, 1);
                };

                if (waitForAGMInstance) {
                    unsub = this._appManager.onInstanceAgmServerReady(waitFunc);
                } else {
                    // if not there subscribe for instance events
                    unsub = this._appManager.onInstanceStarted(waitFunc);
                }

                // try to find it in the current list
                const instances: Glue42.AppManager.Instance = check();
                if (instances) {
                    if (unsub) {
                        unsub();
                        unsub = undefined;
                    }
                    clearTimeout(timeout);
                    resolve(instances);
                }
            };

            this._agm.invoke(AgmNames.StartApplicationMethodName,
                {
                    Name: name,
                    Context: context,
                    Options: options
                },
                "best",
                {
                    methodResponseTimeoutMs: startTimeout
                },
                (result) => {
                    const acsResult: InstanceProps = result.returned && result.returned.Instance_0
                        ? result.returned.Instance_0
                        : result.returned;

                    if (acsResult && acsResult.Id) {
                        if (this._appManager.mode === "startOnly") {
                            // we're in startOnly mode, no stream, so we will simulate the instance
                            // based on the data received from GD
                            const instance = this._appManager.handleInstanceStarted(acsResult);
                            resolve(instance);
                        } else {
                            // we received instance from ACS, let's wait until it appears on the stream
                            waitForApplicationInstance(acsResult.Id);
                        }
                    } else {
                        // we haven't received instance from ACS - this might happen if the app does not
                        // support tracking instances (e.g. we start internet explorer with some url)
                        resolve(undefined);
                    }
                },
                (err: any) => {
                    reject(err);
                });
        });
    }

    public onInstanceStarted(callback: (instance: Glue42.AppManager.Instance) => any): void {
        this._registry.add("instanceStarted", callback);
    }

    public onInstanceStopped(callback: (instance: Glue42.AppManager.Instance) => any): void {
        this._registry.add("instanceStopped", callback);
    }

    public onAvailable(callback: (app: Glue42.AppManager.Application) => any): void {
        this._registry.add("appAvailable", callback);
    }

    public onUnavailable(callback: (app: Glue42.AppManager.Application) => any): void {
        this._registry.add("appUnavailable", callback);
    }

    public onChanged(callback: (app: Glue42.AppManager.Application) => any): void {
        this._registry.add("appChanged", callback);
    }

    public onRemoved(callback: (app: Glue42.AppManager.Application) => any): void {
        this._registry.add("appRemoved", callback);
    }
}
