import { Glue42Core } from "@glue42/core";
import { Environment } from "./../types";
import { default as GD } from "./gd/gd";
import { Glue42 } from "../../../glue";

export default (agm: Glue42Core.AGM.API, logger: Glue42Core.Logger.API, appManagerGetter: () => Glue42.AppManager.API, displayAPIGetter: () => Glue42.Displays.API, gdMajorVersion: number): Promise<Environment> => {
    const _logger = logger;
    return new Promise((resolve, reject) => {
        if (gdMajorVersion === 2) {
            // We are running in HC
            _logger.trace("running in HC");
            throw new Error("GD2 not supported");
        } else if (gdMajorVersion >= 3) {
            // We are running in GD 3
            // TODO expose instance in glu42gd
            _logger.trace("running in GD 3");
            const gd = new GD(agm, _logger, appManagerGetter, displayAPIGetter, undefined, window.glue42gd.windowId);
            gd.init()
                .then(resolve)
                .catch(reject);
        } else {
            // We are running in Browser or Node, we should check which stream is available
            // But we need to pass the container as dependency
            const gdPromise = new GD(agm, _logger, appManagerGetter, displayAPIGetter).init();
            const wait = (ms: number) => new Promise((res, rej) => setTimeout(() => { rej("timeout waiting for streams"); }, ms));
            Promise.race([gdPromise, wait(10000)])
                .then(resolve)
                .catch(reject);
        }
    });
};
