import ApplicationImpl from "./application";
import { Glue42 } from "../../glue";
import { AppProps, InstanceProps } from "./types";
import { default as CallbackRegistryFactory, CallbackRegistry, UnsubscribeFunction } from "callback-registry";
import InstanceImpl from "./instance";
import { Glue42Core } from "@glue42/core";

export default class AppManagerImpl implements Glue42.AppManager.AppManager {

    private _apps: { [key: string]: ApplicationImpl } = {};
    private _instances: InstanceImpl[] = [];
    private _registry: CallbackRegistry = CallbackRegistryFactory();

    constructor(
        public mode: Glue42.AppManager.Mode,
        private _agm: Glue42Core.AGM.API,
        private _activities: Glue42.Activities.API,
        private _windows: Glue42.Windows.API,
        private _logger: Glue42Core.Logger.API,
        private _gdMajorVersion: number
    ) {
        //
    }

    public application = (name: string) => {
        return this._apps[name];
    }

    public applications = () => {
        return Object.keys(this._apps).map((k: string) => this._apps[k]);
    }

    public instances = () => {
        return this._instances;
    }

    public getMyInstance = () => {
        if (this._gdMajorVersion >= 3) {
            // GD3 check
            const instanceId = window.glue42gd.appInstanceId;
            return this._instances.filter((i) => i.id === instanceId)[0];
        }

        return undefined;
    }

    public handleAppAdded = (props: AppProps) => {
        const id = this._getAppId(props);
        this._logger.trace(`adding app ${id}`);
        this._apps[id] = new ApplicationImpl(this, id, this._agm);
        const app = this._updateAppFromProps(props);

        this._registry.execute("appAdded", app);
    }

    public handleAppUpdated = (props: AppProps) => {
        // TODO: check side effects, is this._apps updated?

        const app = this._updateAppFromProps(props);

        this._registry.execute("appChanged", app);
    }

    public handleAppRemoved = (props: AppProps) => {
        const id = this._getAppId(props);
        this._logger.trace(`removing app ${id}`);
        const app = this.application(id);

        // remove all instances related to the app
        this._instances = this._instances.filter((i) => i.application.name !== app.name);

        delete this._apps[id];

        this._registry.execute("appRemoved", app);
    }

    public handleAppReady = (props: AppProps) => {
        const id = this._getAppId(props);
        const app = this._getAppOrThrow(id);
        app.updateFromProps(props);

        if (app.available) {
            this._registry.execute("appAvailable", app);
        } else {
            this._registry.execute("appUnavailable", app);
        }
    }

    public handleInstanceStarted = (props: InstanceProps): InstanceImpl => {
        this._logger.trace(`started app ${props.Name} ${props.Id}`);
        const id = this._getInstanceId(props);
        const appName = this._getInstanceAppName(props);

        const instance = new InstanceImpl(id, appName, this, this._agm, this._activities, this._windows);

        this._updateInstanceFromProps(instance, props);

        this._instances.push(instance);

        this._registry.execute("instanceStarted", instance);
        return instance;
    }

    public handleInstanceStopped = (props: InstanceProps) => {
        this._logger.trace(`failed to start app ${props.Name} ${props.Id}`);

        const id = this._getInstanceId(props);
        const appName = this._getInstanceAppName(props);
        const instance = this._getInstanceOrThrow(id, appName);

        this._instances = this._instances.filter((i) => !this._matchInstance(i, id, appName));

        this._registry.execute("instanceStopped", instance);
        instance.done();
    }

    public handleInstanceAgmServerReady = (props: InstanceProps) => {

        const id = this._getInstanceId(props);
        const appName = this._getInstanceAppName(props);
        const instance = this._getInstanceOrThrow(id, appName);

        instance.updateAgmInstanceFromProps(props);

        this._registry.execute("instanceAgmServerReady", instance);
    }

    public handleInstanceStartFailed = (props: InstanceProps) => {

        const id = this._getInstanceId(props);
        const appName = this._getInstanceAppName(props);
        const startFailed = true;
        const instance = new InstanceImpl(id, appName, undefined, undefined, undefined, undefined, startFailed);

        this._updateInstanceFromProps(instance, props);

        this._registry.execute("instanceStartFailed", instance);
    }

    public handleInstanceUpdated = (props: InstanceProps) => {

        const id = this._getInstanceId(props);
        const app = this._getInstanceAppName(props);
        const instance = this._getInstanceOrThrow(id, app);

        this._updateInstanceFromProps(instance, props);
    }

    /** Event subscription methods */
    public onInstanceStarted = (callback: (item: Glue42.AppManager.Instance) => any): UnsubscribeFunction => {
        return this._registry.add("instanceStarted", callback, this._instances);
    }

    public onInstanceStartFailed = (callback: (item: Glue42.AppManager.Instance) => any): UnsubscribeFunction => {
        return this._registry.add("instanceStartFailed", callback);
    }

    public onInstanceStopped = (callback: (item: Glue42.AppManager.Instance) => any): UnsubscribeFunction => {
        return this._registry.add("instanceStopped", callback);
    }

    public onInstanceUpdated = (callback: (item: Glue42.AppManager.Instance) => any): UnsubscribeFunction => {
        return this._registry.add("instanceChanged", callback);
    }

    public onInstanceAgmServerReady = (callback: (item: Glue42.AppManager.Instance) => any): UnsubscribeFunction => {
        return this._registry.add("instanceAgmServerReady", callback);
    }

    public onAppAdded = (callback: (item: Glue42.AppManager.Application) => any): UnsubscribeFunction => {
        return this._registry.add("appAdded", callback, Object.values(this._apps));
    }

    public onAppRemoved = (callback: (item: Glue42.AppManager.Application) => any): UnsubscribeFunction => {
        return this._registry.add("appRemoved", callback);
    }

    public onAppAvailable = (callback: (item: Glue42.AppManager.Application) => any): UnsubscribeFunction => {
        return this._registry.add("appAvailable", callback);
    }

    public onAppUnavailable = (callback: (item: Glue42.AppManager.Application) => any): UnsubscribeFunction => {
        return this._registry.add("appUnavailable", callback);
    }

    public onAppChanged = (callback: (item: Glue42.AppManager.Application) => any): UnsubscribeFunction => {
        return this._registry.add("appChanged", callback);
    }

    private _getAppOrThrow(id: string) {
        const result = this.application(id);
        if (!result) {
            throw Error(`app with id ${id} not found`);
        }
        return result;
    }

    private _getAppId(props: AppProps) {
        return props.Name;
    }

    private _matchInstance(instance: Glue42.AppManager.Instance, id: string, appName: string) {
        return instance.id === id && instance.application.name === appName;
    }

    private _getInstanceByIdAndName(id: string, appName: string) {
        return this._instances.filter((i) => this._matchInstance(i, id, appName))[0];
    }

    private _getInstanceOrThrow(id: string, appName: string) {
        const result = this._getInstanceByIdAndName(id, appName);
        if (!result) {
            throw Error(`instance with id ${id} not found`);
        }
        return result;
    }

    private _getInstanceId(props: InstanceProps) {
        return props.Id;
    }

    private _getInstanceAppName(props: InstanceProps) {
        return props.Name;
    }

    private _updateAppFromProps(props: AppProps) {
        const id = this._getAppId(props);
        this._logger.trace(`updating app with  + ${id}, ${props}`);
        const app = this._getAppOrThrow(id);

        app.updateFromProps(props);

        return app;
    }

    private _updateInstanceFromProps(instance: InstanceImpl, props: InstanceProps) {
        this._logger.trace("updating instance with " + this._getInstanceId(props) + " for app " + this._getInstanceAppName(props));
        instance.updateFromProps(props);
    }
}
