import { ActivityStatus } from "./activityStatus";

export class EntityEvent<T> {
    public entity: T;
    public context: EntityEventContext;

    constructor(entitiy: T, context: EntityEventContext) {
        this.entity = entitiy;
        this.context = context;
    }
}

export class EntityEventContext {
    public type: string;

    constructor(eventType: string) {
        this.type = eventType;
    }
}

export class ActivityStatusChangeEventContext extends EntityEventContext {
    public oldStatus: ActivityStatus;
    public newStatus: ActivityStatus;

    constructor(newStatus: ActivityStatus, oldStatus: ActivityStatus) {
        super(EntityEventType.StatusChange);
        this.newStatus = newStatus;
        this.oldStatus = oldStatus;
    }
}

export class ActivityContextChangedEventContext extends EntityEventContext {
    public context: object;
    public updated: object;
    public removed: string[];

    constructor(context: string | object, updated: object, removed: string[]) {
        super(EntityEventType.ActivityContextChange);
        this.context = typeof context === "string" ? JSON.parse(context) : context;
        this.updated = updated;
        this.removed = removed;
    }
}

export class EntityEventType {
    public static Added: string = "added";
    public static Removed: string = "removed";
    public static Updated: string = "updated";
    public static Closed: string = "closed";
    public static StatusChange: string = "statusChange";
    public static ActivityContextChange: string = "activityContextUpdate";
    public static ActivityWindowEvent: string = "activityWindowEvent";
    // #deleteme TODO: these are not implemented in HC
    public static ActivityWindowJoinedActivity: string = "joined";
    public static ActivityWindowLeftActivity: string = "left";
}

export class ActivityState {
    public static Created: string = "created";
    public static Started: string = "started";
    public static Destroyed: string = "destroyed";
}
