import { Glue42 } from "../../glue";
import { Glue42Notification } from "./notification";
import { GnsNotificationSettings, GnsNotificationActionSettings, GnsNotificationActionPayload } from "./types";
import generate from "shortid";
import { PanelAPI } from "./panel";
const STARTING_INDEX = 0;

export class Notifications implements Glue42.Notifications.API {
    public panel: PanelAPI;

    private methodsRegistered: boolean = false;
    private readonly methodNameRoot = "T42.Notifications.Handler-" + generate();
    private nextId = 0;
    private notifications: { [id: string]: Glue42Notification } = {};

    constructor(private interop: Glue42.Interop.API) {
        this.panel = new PanelAPI(interop);
    }

    public get maxActions(): number {
        return 2;
    }

    public async raise(options: Glue42.Notifications.Glue42NotificationOptions): Promise<Glue42.Notifications.Glue42Notification> {
        if (!options) {
            throw new Error("invalid options - should be an object");
        }
        if (!options.title) {
            throw new Error("invalid options - should have title");
        }
        if (!this.methodsRegistered) {
            const bunchOfPromises = [];
            for (let index = STARTING_INDEX; index < this.maxActions; index++) {
                bunchOfPromises.push(
                    this.interop.register(this.methodNameRoot + "_" + index, this.handleNotificationAction.bind(this))
                );
            }

            await Promise.all(bunchOfPromises);
            this.methodsRegistered = true;
        }

        // transform actions
        const id = String(this.nextId++);
        const type = options.type ?? "Notification";
        const notification: GnsNotificationSettings = {
            title: options.title,
            type,
            severity: options.severity ?? "None",
            description: options.body,
            glueRoutingDetailMethodName: this.methodNameRoot + "_" + STARTING_INDEX,
            actions: [],
            sourceId: id,
            source: options.source
        };

        if (options.actions) {
            const validActions = options.actions.slice(0, this.maxActions);

            let index = STARTING_INDEX;
            for (const action of validActions) {
                const args: GnsNotificationActionPayload = { // this should include user arguments (action.interop?.arguments)
                    g42notificationId: id,
                    g42action: action.action,
                    g42interopMethod: action.interop?.method,
                    g42interopTarget: action.interop?.target,
                };
                // TODO, properly convert between args and GNS value
                const parameters = Object.keys(args).map((key) => {
                    const value = (args as any)[key];
                    return {
                        name: key,
                        value: {
                            stringValue: value
                        }
                    };
                });

                const glueAction: GnsNotificationActionSettings = {
                    name: this.methodNameRoot + "_" + index,
                    description: action.title,
                    displayName: action.title,
                    parameters
                };

                notification.actions.push(glueAction);
                index++;
            }
        }

        if (options.icon) {
            notification.attributes = notification.attributes || [];
            notification.attributes.push(
                { key: "icon", value: { stringValue: options.icon } }
            );
        }

        if (options.data) {
            notification.attributes = notification.attributes || [];
            const dataAsString = JSON.stringify(options.data);
            notification.attributes.push(
                { key: "data", value: { stringValue: dataAsString } }
            );
        }

        await this.interop.invoke("T42.GNS.Publish.RaiseNotification", { notification });

        const g42notification = new Glue42Notification(options);
        this.notifications[id] = g42notification;
        return g42notification;
    }

    public async setFilter(filter: Glue42.Notifications.NotificationFilter): Promise<Glue42.Notifications.NotificationFilter> {
        const result = await this.interop.invoke("T42.Notifications.Filter", filter);
        return result.returned;
    }

    public async getFilter(): Promise<Glue42.Notifications.NotificationFilter> {
        const result = await this.interop.invoke("T42.Notifications.Filter");
        return result.returned;
    }

    private handleNotificationAction(args: any) {
        if (args.g42notificationId) {
            // action click
            const payload: GnsNotificationActionPayload = args as GnsNotificationActionPayload;
            const id = payload.g42notificationId;
            const notification = this.notifications[id];
            if (!notification) {
                return;
            }

            // user event
            const event = new Event("onaction");
            (event as Glue42.Notifications.ActionEvent).action = payload.g42action;
            if (notification.onaction) {
                notification.onaction(event as Glue42.Notifications.ActionEvent);
            }

            // interop
            const actionDefinition = (notification.actions || []).find((item) => item.action === payload.g42action);
            const actionInterop = actionDefinition.interop;
            if (actionInterop) {
                this.interop.invoke(actionInterop.method, actionInterop.arguments || {}, actionInterop.target || "best");
            }

            // dispatch
            notification.dispatchEvent(event);
        } else if (args.notification && args.notification.sourceNotificationId) {
            // notification click
            const id = args.notification.sourceNotificationId;
            const notification = this.notifications[id];
            if (!notification) {
                return;
            }

            // user event
            const event = new Event("onclick");
            if (notification.onclick) {
                notification.onclick(event);
            }

            // interop call
            const clickInterop = notification.options.clickInterop;
            if (clickInterop) {
                this.interop.invoke(clickInterop.method, clickInterop.arguments || {}, clickInterop.target || "best");
            }

            // dispatch
            notification.dispatchEvent(event);
        }
    }
}
