import { Glue42 } from "../../glue";
import { Glue42Core } from "@glue42/core";
import CaptureOptions = Glue42.Displays.CaptureOptions;

const T42DisplayCommand = "T42.Displays.Command";

export default class DisplayManager implements Glue42.Displays.API {
    constructor(
        private _agm: Glue42Core.AGM.API,
        private _logger: Glue42Core.Logger.API,
    ) {
        //
    }

    public all = async (): Promise<Glue42.Displays.Display[]> => {
        const displays = await this.callGD<Glue42.Displays.Display[]>(DisplayCommand.GetAll, {});
        return displays.map(this.decorateDisplay);
    }

    public get = async (id: number): Promise<Glue42.Displays.Display> => {
        const display = await this.callGD<Glue42.Displays.Display>(DisplayCommand.Get, { id });
        return this.decorateDisplay(display);
    }

    public getPrimary = async (): Promise<Glue42.Displays.Display> => {
        const primary = (await this.all()).find((d) => d.isPrimary);
        return primary;
    }

    public getByWindowId(id: string): Promise<Glue42.Displays.Display> {
        const current = this.callGD<Glue42.Displays.Display>(DisplayCommand.GetByWindowId, { id });
        return current;
    }

    public capture = async (options: CaptureOptions): Promise<string> => {
        const screenshot = await this.callGD<string>(DisplayCommand.Capture, { ...options });
        return screenshot;
    }

    public captureAll = async (options: Glue42.Displays.CaptureAllOptions): Promise<string | string[]> => {
        const screenshots = await this.callGD<string | string[]>(DisplayCommand.CaptureAll, { ...options });
        return screenshots;
    }

    public getMousePosition = async (): Promise<Glue42.Displays.Point> => {
        const point = await this.callGD<Glue42.Displays.Point>(DisplayCommand.GetMousePosition);
        return point;
    }

    private callGD = async <T>(command: string, options?: object): Promise<T> => {
        // Consider targetting only GD
        const invocationResult = await this._agm.invoke(T42DisplayCommand, { options: { ...options }, command });
        return invocationResult.returned.data;
    }

    private decorateDisplay = (original: Glue42.Displays.Display): Glue42.Displays.Display => {
        return {
            ...original,
            capture: (size: SizeOptions) => this.capture({ id: original.id, size })
        };
    }
}

type SizeOptions = Glue42.Displays.ScaleOptions | Glue42.Displays.AbsoluteSizeOptions;

enum DisplayCommand {
    Capture = "capture",
    CaptureAll = "captureAll",
    GetAll = "getAll",
    Get = "get",
    GetByWindowId = "getByWindowId",
    GetMousePosition = "getMousePosition",
}
